export default {
  name: 'modal',
  props: {
    name: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      default: '',
    },
    effect: {
      type: String,
      default: 'from-top',
    },
    closeButtonHtml: {
      type: String,
      default: '',
    },
    closeOn: {
      type: Boolean,
      default: false,
    }
  },
  computed: {
    effectName() {
      return `modal-${this.effect}`;
    }
  },
  data() {
    return {
      show: false,
      body: document.querySelector('body'),
    };
  },
  watch: {
    show(shown) {
      if (shown) {
        this.body.classList.add('modal-shown');
      } else {
        this.body.classList.remove('modal-shown');
      }
    },
  },
  methods: {
    toggleModal(name, action = 'toggle') {
      if (name !== this.name) return;
      action = action.toLowerCase();
      if (action === 'show') {
        this.show = true;
      } else if (action === 'close') {
        this.show = false;
      } else {
        this.show = !this.show;
      }
    },
    onKeydown(event) {
      // escape: 27
      if (this.show && event.keyCode == 27) {
        event.preventDefault();
        this.show = false;
      }
    },
  },
  beforeMount() {
    window.addEventListener('keydown', this.onKeydown.bind(this));
    this.$root.$on('modal-toggle', this.toggleModal.bind(this));
    this.$on('close', () => {
      this.$root.$emit('modal-toggle', this.name, 'close');
    });

    if (typeof window.Moss.modal === 'undefined') {
      window.Moss.modal = {
        show: (name) => {
          this.$root.$emit('modal-toggle', name, 'show');
        },
        close: (name) => {
          this.$root.$emit('modal-toggle', name, 'close');
        },
        toggle: (name, action = 'toggle') => {
          this.$root.$emit('modal-toggle', name, action);
        }
      };
    }

    if (this.closeOn) {
      window.addEventListener('click', event => {
        if (event.target.classList.contains('modal-mask')) {
          this.$root.$emit('modal-toggle', this.name, 'close');
        }
      });
    }
  }
}



