export default {
  name: 'tree-item',
  props: {
    item: {
      type: Object,
      required: true,
    },
    openedHtml: {
      type: String,
      default: '<i class="fa fa-angle-down fa-fw" aria-hidden="true"></i>',
    },
    closedHtml: {
      type: String,
      default: '<i class="fa fa-angle-right fa-fw" aria-hidden="true"></i>',
    },
  },
  data() {
    return {
      open: false,
    };
  },
  computed: {
    hasItems() {
      return this.item.items && this.item.items.length;
    },
    itemClass() {
      return {
        'tree-item': true,
        'has-items': this.hasItems,
      };
    },
  },
  methods: {
    toggle() {
      if (this.hasItems) {
        this.open = !this.open;
      }
    },
  },
}



