import Util from '../lib/util';
import ElementMeasurer from 'element-measurer';

export default {
  name: 'scroll-to',
  props: {
    duration: {
      type: Number,
      default: 600,
    },
    easing: {
      type: String,
      default: 'easeOutCubic',
    },
    offset: {
      type: Number,
      default: 0,
    },
    topHtml: {
      type: String,
      default: '',
    },
    bottomHtml: {
      type: String,
      default: '',
    },
    disableTop: {
      type: Boolean,
      default: false,
    },
    disableBottom: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      showToTop: true,
      showToBottom: true,
      docSize: new ElementMeasurer(document),
    };
  },
  methods: {
    toTop() {
      Util.scrollIt(0, this.duration, this.easing);
    },
    toBottom() {
      Util.scrollIt(this.docSize.maxScrollTop, this.duration, this.easing);
    },
    onScroll() {
      this.showToTop = this.docSize.scrollTop >= this.offset;
      this.showToBottom = (this.docSize.maxScrollTop - this.docSize.scrollTop)
        >= this.offset;
    }
  },
  mounted() {
    if (this.offset) {
      window.addEventListener('scroll', this.onScroll.bind(this));
      this.onScroll(); // invoke once.
    }
  }
}



